<template>
  <div id="app">
    <NavBar :isAuthenticated="isAuthenticated" @userAuthenticated="toggleAuthenticated" />
    <router-view :isAuthenticated="isAuthenticated" @userAuthenticated="toggleAuthenticated" :users="users" :user="user" />
  </div>
</template>

<script>
import NavBar from './components/NavBar'
import { authService } from './services/Auth';
import data from "../data/galleries.json"

export default {
  components: {
    NavBar
  },

  data(){
    return {
      isAuthenticated: authService.isAuthenticated(),
      users: [],
      user: {}
    }
  },
  created() {
   
      data.data.forEach(element => {
        this.users.push(element.user)
        
      });
    
  },
   methods: {
    toggleAuthenticated(value){
      this.isAuthenticated = value
    }
  }
}
</script>




